import Axios from 'axios';
import * as Const from './Const';
import _ from 'lodash';

export async function GetToken (email, password) {
  // get user token
  const userEmailPassword = `${email}:${password}`;
  const base64UserEmailPassword = window.btoa(userEmailPassword);
  const authorization = `Basic ${base64UserEmailPassword}`;

  const url = `${process.env.REACT_APP_STOREGANISE_API_BASE_URL}/v1/auth/token?include=settings,customFields,files`;
  const requestConfig = {
    headers: {
      Accept: 'application/json',
      Authorization: authorization
    }
  };
  const response = await Axios.post(url, {}, requestConfig);
  return response.data.accessToken;
}

export async function CreateUser (externalId, data, password) {
  const url = `${process.env.REACT_APP_STOREGANISE_API_BASE_URL}/v1/users`;
  let requestData = GetUserMainData(data);

  let startDate = `${data.startDateItem.getFullYear()}-`;
  const month = data.startDateItem.getMonth() + 1;
  const date = data.startDateItem.getDate();
  startDate = startDate + (month < 10 ? '0' : '') + month + '-';
  startDate = startDate + (date < 10 ? '0' : '') + date;

  const requestConfig = {
    headers: {
      'content-type': 'application/json'
    }
  };

  const customFieldsValue = GenerateCustomFieldData(data);

  requestData = { ...requestData, customFields: customFieldsValue };
  const response = await Axios.post(url, { ...requestData, password, startDate, personalId: externalId, language: 'ja' }, requestConfig);
  return response.data;
}

export function GetPassword (data) {
  let password = '';
  if (data.customerType === Const.CustomerType.Personal) {
    password = data.personalInfoItem.password;
  } else {
    password = data.companyInfoItem.password;
  }
  return password;
}

export async function UploadFile (file, accessToken) {
  const url = `${process.env.REACT_APP_STOREGANISE_API_BASE_URL}/v1/uploads`;

  if (file !== undefined) {
    const formData = new FormData();
    formData.append('file', file);

    const requestConfig = {
      headers: {
        'content-type': 'multipart/form-data',
        Authorization: `Bearer ${accessToken}`
      }
    };

    const response = await Axios.post(url, formData, requestConfig);
    return response.data;
  }
}

export async function CreateUnitOrder (shopId, startDate, unitTypeId, accessToken) {
  const url = `${process.env.REACT_APP_STOREGANISE_API_BASE_URL}/v1/units/orders`;
  const requestData = {
    siteId: shopId,
    startDate,
    unitTypeId
  };
  const requestConfig = {
    headers: {
      'content-type': 'application/json',
      Authorization: `Bearer ${accessToken}`
    }
  };

  await Axios.post(url, requestData, requestConfig);
}

function GetUserMainData (data) {
  if (data.customerType === Const.CustomerType.Personal) {
    const personalInfo = data.personalInfoItem;

    return {
      email: personalInfo.email,
      firstName: personalInfo.firstName,
      lastName: personalInfo.lastName,
      phone: _.get(data, 'customerPhone', '')
    };
  }
}

export function GetNote (data) {
  const items = [];

  items.push(`ご希望の店舗\t${data.shopUnitInfo.shopName}`);
  items.push(`部屋タイプ\t${data.shopUnitInfo.unitTypeName}`);
  items.push(`利用開始希望日\t${data.startDateItem.startDate.year}年${data.startDateItem.startDate.month}月${data.startDateItem.startDate.date}日`);

  const overallItem = data.overallItem;
  if (overallItem.paymentMethod === Const.PaymentMethod.CreditCard) {
    items.push('月々支払い方法\tクレジット');
  } else if (overallItem.paymentMethod === Const.PaymentMethod.BankTransfer) {
    items.push('月々支払い方法\t口座振替');
  }

  if (data.customerType === Const.CustomerType.Personal) {
    const personalInfo = data.personalInfoItem;
    items.push('お客様情報-個人');

    items.push(`氏名\t${personalInfo.lastName} ${personalInfo.firstName}`);
    items.push(`フリガナ\t${personalInfo.lastNameKana} ${personalInfo.firstNameKana}`);
    items.push(`メールアドレス\t${personalInfo.email}`);

    const mobilePhone = `${personalInfo.mobilePhone1}-${personalInfo.mobilePhone2}-${personalInfo.mobilePhone3}`;
    const tel = `${personalInfo.tel1}-${personalInfo.tel2}-${personalInfo.tel3}`;
    items.push(`ご連絡先 - 携帯\t${mobilePhone}`);
    items.push(`ご連絡先 - 固定\t${tel}`);

    const fax = `${personalInfo.fax1}-${personalInfo.fax2}-${personalInfo.fax3}`;
    items.push(`ご連絡先 - FAX\t${fax}`);

    const postalCode = `${personalInfo.postalCode1}-${personalInfo.postalCode2}`;
    items.push(`郵便番号\t${postalCode}`);
    items.push(`住所\t${personalInfo.address}`);

    const sex = (personalInfo.sex === Const.Sex.Male ? '男性' : (personalInfo.sex === Const.Sex.Female ? '女性' : ''));
    items.push(`性別\t${sex}`);

    const birthDate = `${personalInfo.birthDate.year}-${personalInfo.birthDate.month}-${personalInfo.birthDate.date}`;
    items.push(`生年月日\t${birthDate}`);

    if (personalInfo.additionalContactType === Const.PersonalAdditionalContactType.Relative) {
      items.push('第２連絡先 - 親族');
      items.push(`親族のお名前\t${personalInfo.relativeLastName} ${personalInfo.relativeFirstName}`);
      items.push(`親族のフリガナ\t${personalInfo.relativeLastNameKana} ${personalInfo.relativeFirstNameKana}`);

      const relativePhone = `${personalInfo.relativePhone1}-${personalInfo.relativePhone2}-${personalInfo.relativePhone3}`;
      items.push(`親族の電話番号\t${relativePhone}`);
      items.push(`親族の続柄\t${personalInfo.relativeRelationship}`);
    }

    if (personalInfo.additionalContactType === Const.PersonalAdditionalContactType.Company) {
      items.push('第２連絡先 - 勤務先');
      items.push(`勤務先\t${personalInfo.companyName}`);
      items.push(`勤務先フリガナ\t${personalInfo.companyNameKana}`);

      const companyPhone = `${personalInfo.companyPhone1}-${personalInfo.companyPhone2}-${personalInfo.companyPhone3}`;
      items.push(`勤務先電話番号\t${companyPhone}`);
    }
  } else if (data.customerType === Const.CustomerType.Company) {
    const companyInfoItem = data.companyInfoItem;
    items.push('お客様情報-法人');

    items.push(`社名\t${companyInfoItem.companyName}`);
    items.push(`社名（フリガナ）\t${companyInfoItem.companyNameKana}`);
    items.push(`代表者名\t${companyInfoItem.repLastName} ${companyInfoItem.repFirstName}`);
    items.push(`代表者名（フリガナ）\t${companyInfoItem.repLastNameKana} ${companyInfoItem.repFirstNameKana}`);
    items.push(`メールアドレス\t${companyInfoItem.email}`);

    const tel = `${companyInfoItem.tel1}-${companyInfoItem.tel2}-${companyInfoItem.tel3}`;
    items.push(`ご連絡先（電話番号）\t${tel}`);

    const fax = `${companyInfoItem.fax1}-${companyInfoItem.fax2}-${companyInfoItem.fax3}`;
    items.push(`ご連絡先（FAX）\t${fax}`);

    const postalCode = `${companyInfoItem.postalCode1}-${companyInfoItem.postalCode2}`;
    items.push(`郵便番号\t${postalCode}`);
    items.push(`住所\t${companyInfoItem.address}`);

    if (companyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.PersonInCharge) {
      items.push('第２連絡先 - 担当者');
      items.push(`担当者お名前\t${companyInfoItem.personInChargeLastName} ${companyInfoItem.personInChargeFirstName}`);
      items.push(`担当者お名前(フリガナ)\t${companyInfoItem.personInChargeLastNameKana} ${companyInfoItem.personInChargeFirstNameKana}`);

      const personInChargePhone = `${companyInfoItem.personInChargePhone1}-${companyInfoItem.personInChargePhone2}-${companyInfoItem.personInChargePhone3}`;
      items.push(`担当者電話番号\t${personInChargePhone}`);
      items.push(`担当者所属\t${companyInfoItem.personInChargeDepartment}`);
    }

    if (companyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.Relative) {
      items.push('第２連絡先 - 代表者ご親族');
      items.push(`代表者のご親族様のお名前\t${companyInfoItem.relativeLastName} ${companyInfoItem.relativeFirstName}`);
      items.push(`代表者のご親族様のお名前(フリガナ)\t${companyInfoItem.relativeLastNameKana} ${companyInfoItem.relativeFirstNameKana}`);

      const relativePhone = `${companyInfoItem.relativePhone1}-${companyInfoItem.relativePhone2}-${companyInfoItem.relativePhone3}`;
      items.push(`代表者のご親族様の電話番号\t${relativePhone}`);
      items.push(`代表者のご親族様の続柄\t${companyInfoItem.relationship}`);
    }

    const additionalContactFirstText = (companyInfoItem.additionalContactFirst === true ? 'はい' : 'いいえ');
    items.push(`第2連絡先を優先的な連絡先としますか？\t${additionalContactFirstText}`);
  }

  items.push(`ご利用期間\t${overallItem.usagePeriod}`);
  items.push(`その他キャンペーン希望\t${overallItem.otherCampaign}`);

  items.push(`予定保管物品(その他)\t${overallItem.storageItemOther}`);

  items.push(`保管目的\t${overallItem.purpose}`);
  items.push(`保管目的(その他)\t${overallItem.purposeOther}`);

  items.push(`何をごらんになって申し込みましたか\t${overallItem.viewedMedias}`);
  items.push(`何をごらんになって申し込みましたか(その他)\t${overallItem.viewedMediaOther}`);

  items.push(`当店舗を選んだ理由(その他)\t${overallItem.rentalReasonOther}`);

  items.push(`その他連絡事項\t${overallItem.otherMessage}`);

  let note = '';
  items.forEach(element => {
    note += `${element}\r\n`;
  });

  return note;
}

export function GenerateCustomFieldData (data) {
  const customFields = Const.CustomFields;
  const overallItem = data.overallItem;

  let customFieldValues = {};
  for (const customField of Object.values(customFields)) {
    customFieldValues = { ...customFieldValues, [customField]: null };
  }

  customFieldValues[customFields.Phone] = overallItem.customerPhone;

  if (data.customerType === Const.CustomerType.Personal) {
    // お客様情報-個人
    const personalInfo = data.personalInfoItem;

    customFieldValues[customFields.ContactType] = '個人';

    // 氏名 - フリガナ
    customFieldValues[customFields.FirstNameFurigana] = `${personalInfo.firstNameKana}`;
    customFieldValues[customFields.LastNameFurigana] = `${personalInfo.lastNameKana}`;
  }

  return customFieldValues;
}
