import React from 'react';
import './shop/shop.css';
import Header from './Header';
import axios from 'axios';
import _ from 'lodash';

export default class Main extends React.Component {
  constructor () {
    super();

    this.state = {
      shops: undefined
    };
  }

  async componentDidMount () {
    const url = `${process.env.REACT_APP_STOREGANISE_API_BASE_URL}/v1/settings`;
    const response = await axios.get(url);

    this.setState({ shops: _.get(response, 'data.sites') });
  }

  render () {
    let shopList = null;
    if (this.state.shops) {
      shopList = this.state.shops.map(x =>
        <div key={_.get(x, 'id')}><a className='shop-list-item' href={`#/shop/${_.get(x, 'id')}`}>{_.get(x, 'title.ja')}店</a></div>
      );
    }

    return (
      <>
        <Header />
        <article className='service'>
          <section className='contents'>
            <div className='container'>
              {shopList}
            </div>
          </section>
        </article>
      </>
    );
  }
}
