import React from 'react';
import UserInfoPersonal from './UserInfoPersonal';
import FormGroup from '../FormGroup';
import * as Const from '../../Utils/Const';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

export default class Entry extends React.Component {
  render () {
    const {
      StartDateItem,
      CustomerType,
      SubmitHandler
    } = this.props;

    return (
      <div className='row'>
        <div className='col-md-10 col-md-offset-1 col-sm-10 col-sm-offset-1'>
          <div className='form-horizontal'>
            <FormGroup title='利用開始希望日' isRequired>
              <div className='row'>
                <div className='col-sm-12 col-xs-12'>
                  <div className='input-group'>
                    <DatePicker
                      closeOnScroll
                      minDate={new Date()}
                      dateFormat='yyyy/MM/dd'
                      selected={StartDateItem || new Date()}
                      onChange={(date) => this.props.OverallHandlers.updateStartDateHandler(date)}
                    />
                  </div>
                </div>
              </div>
            </FormGroup>

            {CustomerType === Const.CustomerType.Personal && <UserInfoPersonal {...this.props} />}

            <div className='form-group action'>
              <div className='col-sm-12 col-sm-offset-5'>
                <input
                  onClick={SubmitHandler}
                  type='submit'
                  value='お申し込み'
                  className='btn btn-warning btn-lg'
                />
              </div>
            </div>
          </div>
        </div>

      </div>
    );
  }
}
