import React from 'react';
import { Button } from 'react-bootstrap';

export default function PriceList (props) {
  const {
    unitTypeList,
    entryClickHandler
  } = props;

  if (unitTypeList !== undefined) {
    unitTypeList.sort((item1, item2) => item1.price - item2.price);
  }

  return (
    <>
      <div className='price'>
        <table>
          <thead>
            <tr>
              <th className='price_number'>畳数</th>
              <th className='price_official'>正規料金</th>
              <th className='price_deposit'>保証金</th>
              <th className='price_order'>お申し込み</th>
            </tr>
          </thead>

          <tbody id='price_table'>
            {
              unitTypeList !== undefined &&
                  unitTypeList.map(ut => {
                    // var initFee;
                    return (
                      <tr key={ut.id}>
                        <td className='price_number'>{ut.size}</td>
                        <td className='price_official'>{ut.priceDisp} 円</td>
                        <td className='price_deposit'>{ut.deposit} 円</td>
                        <td className='price_order'>
                          {ut.canApply === true
                            ? <Button onClick={entryClickHandler.bind(this, ut.id)} className='btn btn-success' style={{ width: '140px', cursor: 'pointer', borderRadius: '0' }}>お申し込み</Button>
                            : <Button className='btn btn-secondary' style={{ width: '140px', borderRadius: '0' }} disabled><span className='button-text'>ご利用中</span></Button>}
                        </td>
                      </tr>
                    );
                  })
            }
          </tbody>
        </table>
      </div>
    </>
  );
}
